.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: var(--z-fixed);
  background-color: var(--body-color);
}

.nav {
  height: calc(var(--header-height) + 1.5rem);
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 1rem;
}

.nav_left {
  width: 40%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nav_logo {
  width: 40px;
}

.nav_language-selector {
  font-family: inherit;
  font-size: inherit;
  outline: none;
  cursor: pointer;
  background-color: var(--body-color);
  padding: 0.2rem;
  border: 0px;
  border-radius: 0.25em;
}

.nav_list {
  display: flex;
  column-gap: 2rem;
}

.nav_link {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: var(--small-font-size);
  color: var(--title-color);
  font-weight: var(--font-medium);
  transition: 0.3s;
}

.nav_icon, .nav_close, .nav_toggle {
  display: none;
}

.active-link, .nav_link:hover {
  color: var(--title-color-dark);
}

@media screen and (max-width: 768px){
  .header {
    bottom: initial;
    top: 0;
  }

  .nav {
    height: var(--header-height);
  }

  .nav_logo {
    width: 35px;
  }

  .nav_menu {
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    background-color: var(--body-color);
    padding: 2rem 1.5rem 4rem;
    box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.15);
    border-radius: 0 0 1.5rem 1.5rem;
    transition: 0.3s;
  }

  .nav_icon {
    font-size: 1.2rem;
  }

  .nav_icon, .nav_close, .nav_toggle {
    display: block;
  }

  .show-menu {
    top: 0;
  }

  .nav_list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }

  .nav_close {
    position: absolute;
    right: 1.3rem;
    bottom: 0.5rem;
    font-size: 1.5rem;
    cursor: pointer;
    color: var(--title-color);
  }

  .nav_close:hover {
    color: var(--title-color-dark);
  }

  .nav_toggle {
    font-size: 1.1rem;
    cursor: pointer;
  }

  .nav_icon, .nav_close, .nav_toggle {
    display: block;
  }
}